/**
* 添加和编辑
* @module 案件
* @desc 票据管理添加删除
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                center
                width="550px"
        >
            <el-form
                    :model="form"
                    :rules="rules"
                    label-width="140px"
                    ref="ruleForm"
                    size="medium"
                    v-loading="loading"
            >
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="定额发票开始号：" prop="quotaInvoiceStart">
                            <el-input
                                        placeholder="起始发票号"
                                        v-model="form.quotaInvoiceStart"
                                ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="定额发票结束号：" prop="quotaInvoiceEnd">
                            <el-input  placeholder="结束发票号"
                                          v-model="form.quotaInvoiceEnd"></el-input>
                        </el-form-item>
                    </el-col>
                    
                    <el-col :span="24">
                        <el-form-item label="定额发票面额：" prop="quotaInvoiceAmount">
                            <el-select placeholder="请选择发票面额" v-model="form.quotaInvoiceAmount">
                                <el-option
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        v-for="item in quotaInvoiceNumArr"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiCaseQuotaInvoiceUpdate, apiGetCaseQuotaInvoice, apiCaseQuotaInvoiceAdd} from "../../../../../api/case";

    export default {
        name: "edit",
        props: {
            caseProcessList: {
                type: Array,
                default: []
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            }
        },

        data() {
            return {
                state1: "",
                restaurants: [],
                loading: true,
                quotaInvoiceNumArr: [
                    {value: "10", label: "10"},
                    {value: "20", label: "20"},
                    {value: "50", label: "50"},
                    {value: "100", label: "100"}
                ],
                //发送数据
                form: {
                    type: "",
                    quotaInvoiceNum: "",
                    startQuotaInvoiceCode: "",
                    endQuotaInvoiceCode: "",
                    quotaInvoiceAmount: "",
                    isEffective: ""
                },
                //表单验证规则
                rules: {
            
                    quotaInvoiceStart: [
                        {required: true, message: "请输入发票开始编号", trigger: "blur"},
                        {min: 8, max: 8, message: "发票代码必须8位数字", trigger: "blur"}
                    ],
                    quotaInvoiceEnd: [
                        {required: true, message: "请输入发票结束编号", trigger: "blur"},
                        {min: 8, max: 8, message: "发票代码必须8位", trigger: "blur"}
                    ],
                    quotaInvoiceAmount: [
                        {required: true, message: "请选择发票额度", trigger: "change"}
                    ],
                }
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },

            //提交
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        if (this.type) {
                             //编辑
                            this.loading = true;
                            apiCaseQuotaInvoiceUpdate(this.form).then(res => {
                                this.$emit('getList');
                                this.$message({
                                    type: "success",
                                    message: "修改成功!"
                                });
                                this.loading = false;
                                this.closeDialog();
                            });
                        } else {
                            //添加
                            if(this.form.quotaInvoiceStart>this.form.quotaInvoiceEnd){
                                this.$message({ type: "warning", message: "定额发票开始编号不能大于结束编号 !"});
                            }else if(this.form.quotaInvoiceEnd-this.form.quotaInvoiceStart>1000){
                                this.$message({ type: "warning", message: "一次添加定额发票不得超过1000张 !"});                                
                            }else{
                                this.loading = true;
                                apiCaseQuotaInvoiceAdd(this.form).then(res => {
                                    this.$emit('getList');
                                    this.$message({
                                        type: "success",
                                        message: "添加成功!"
                                    });
                                    this.loading = false;
                                    this.closeDialog();
                                });
                            }
                            
                        }
                    }
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                if (this.type) {
                    apiGetCaseQuotaInvoice({quotaId:this.type,pageNum:1,pageSize:20}).then(res => {
                        this.loading = false;
                    });
                } else {
                    this.loading = false;
                    this.form = {
                        type: "1",
                        quotaInvoiceCode: "",
                        startQuotaInvoiceCode: "",
                        endQuotaInvoiceCode: "",
                        quotaInvoiceNum: "",
                        isEffective: "1"
                    };
                }
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .ipt-css {
        width: 150px
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .bill {
        display: flex;
    }
</style>


