/**
* 案件->票据管理
* @module 案件
* @desc 票据管理表格
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="search-wrop">
            <lj-search-from :onAdd="buttonCheck('add')?onAdd:''" :toVoid="buttonCheck('void')?toVoid:''" :onDel="buttonCheck('batchdel')?onDel:''" 
                            :searchObj="searchObj" @searchList="searchList"  />
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}" v-loading='loading'
                  @selection-change="handleSelectionChange" @row-click="seeFunction"
                  border size="mini">
            <el-table-column type="selection" width="55" />
            <el-table-column align="center" label="面额" prop="quotaInvoiceAmount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="起讫号" prop="quotaInvoiceNum" show-overflow-tooltip/>
            <el-table-column align="center" label="份数" prop="quotaInvoiceCount" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="上缴份数" prop="submittedNum" show-overflow-tooltip width="80"/>
            <el-table-column align="center" label="入库日期" prop="createTime" show-overflow-tooltip width="180">
                <template slot-scope="scope">
                    {{scope.row.createTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="入库状态" prop="quotaStatus" width="100">
                <template slot-scope="scope">
                    {{scope.row.quotaStatus | quotaStatus}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作人" prop="createUser" show-overflow-tooltip width="100"/>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                icon="el-icon-search"
                                type="primary"
                                v-if="buttonCheck('see')"
                        ></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link @click.stop="delateFunction(scope.$index, scope.row)" class="el-icon-delete btn-css"
                                 type="danger" v-if="buttonCheck('delete')"><i></i></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                       class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-Edit :caseProcessList="caseProcessList" :show.sync="editObj.show" :title="editObj.title"
                     :type="editObj.editId" @getList="getList"/>
        <!-- 查看 -->
        <seecontent :caseProcessList="caseProcessList" :caseTypeList='caseTypeList' :show.sync="seeObj.show"
                    :title="seeObj.title" :id="seeObj.editId" />
               
    </div>
</template>

<script>
    import LjPagination from '../../../../../components/LjPagination'
    import LjSearchFrom from '../../../../../components/LjSearchForm'
    import changeEdit from "./editadd";
    import seecontent from "./seecontent";
    import {
        apiCaseQuotaInvoiceBatchDelete,
        apiCaseQuotaInvoiceDelete,
        apiCaseQuotaInvoiceSearch,
        apiCaseQuotaInvoiceUpdate
    } from "../../../../../api/case";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    export default {
        name: "addBill",
        data() {
            return {
                caseProcessList: [],
                docStyleArr: [],
                loading: true,
                searchObj: [
                    {type: 'input', name: 'quotaInvoiceStart', label: '开始号码'},
                    {type: 'input', name: 'quotaInvoiceEnd', label: '结束号码'},
                     {
                        type: 'date',
                        dateType: 'daterange',
                        name: 'timeRange',
                        name1: 'startTime',
                        name2: 'endTime',
                        options: [],
                        label: '入库时间',
                        span: 12
                    },
                    // {
                    //     type: 'select', name: 'quotaInvoiceAmount', options: [
                    //         {value: '10', label: '10'},
                    //         {value: '20', label: '20'},
                    //         {value: '50', label: '50'},
                    //         {value: '100', label: '100'},
                    //     ], label: '发票面额'
                    // },
                ],
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseTypeList: [],
                form: {
                    pageSize: 20,
                    pageNum: 1,
                },
                total: 0,
                list: [],
                idstr: '',
            }
        },
        components: {
             LjPagination, LjSearchFrom,changeEdit,seecontent
        },

        methods: {
            //作废
            toVoid(){
               this.$confirm('此操作将永久作废票据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceUpdate({quotaId:this.idstr,quotaStatus:3}).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '作废操作成功!'
                        });
                    })
                })
            },
            //批量选择
            handleSelectionChange(val) {
                let idarr = [];
                val.forEach(item => {
                    idarr.push(item.quotaId);
                });
                this.idstr = idarr.join(',')
            },

            //批量上传
            onImport() {
                this.seeObj.title = '批量上传';
                this.seeObj.show = true;
            },

            //查询+搜索
            getList() {
                apiCaseQuotaInvoiceSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                })
            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.quotaId;
                this.seeObj.title = '查看票据列表';
                this.seeObj.show = true;
            },

            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增票据';
                this.editObj.show = true;
            },

            //编辑
            editFunction(index, row) {
                this.editObj.editId = row.quotaId;
                this.editObj.title = '修改票据';
                this.editObj.show = true;
            },
            
            //删除按钮事件
            delateFunction(index, data) {
                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceDelete(data.quotaId).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },

            //批量删除
            onDel() {
                this.$confirm('此操作将永久删除这些数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceBatchDelete({quotaIds: this.idstr}).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    })
                })
            },
            //
           refreshList(){},
            //搜索点击
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                apiCaseQuotaInvoiceSearch(this.form).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },
        },

        filters: {
            quotaStatus(val) {
                return quotaStatus[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd - hh:mm:ss')
            },

        },
        created() {
            this.getList();

        },
    }
</script>

<style scoped>
    .search-wrop {
        margin-bottom: 10px
    }

    .btn-css {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
